import React, { useState } from "react";
import { CgImage } from "react-icons/cg";
import Loading from "../../components/Loading/Loading";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import Table from "../../components/Table/Table";
import ViewPDF from "../../components/ViewPDF/ViewPDF";
import { isEmptyOrSpaces } from "../../shared/dataTest";
import { UserDocumentsInterface } from "../../shared/interfaces";
import { API } from "../../utils/api";
import { fullScreen, getFileType, isFileImage } from "../../utils/functions";
import NoSession from "../SessionSettings/NoSession";
import { PositionRequirementTableInterface } from "./ViewPosition";

interface RequirementProps {
  requirements: PositionRequirementTableInterface[];
  userDocs: UserDocumentsInterface[];
}

const ViewDocRequirements: React.FC<RequirementProps> = (props) => {
  const [viewDoc, setViewDoc] = useState<UserDocumentsInterface | null>(null);
  const [loadingImage, setLoadingImage] = useState<boolean>(false);
  const [isFullscreen, setSetFullscreen] = useState<boolean>(false);
  const [failedToLoadFile, setFailedToLoadFile] = useState<boolean>(false);

  const isDocumentExist = (
    category_id: string,
    sub_category_id: string,
    field_id: string
  ): UserDocumentsInterface | undefined => {
    if (!isEmptyOrSpaces(category_id)) {
      let userDocs: UserDocumentsInterface[] = props.userDocs.filter(
        (item) => item.category_id === category_id
      );

      if (!isEmptyOrSpaces(sub_category_id)) {
        userDocs = userDocs.filter(
          (item) =>
            item.validation_status === "approved" &&
            item.category_id === category_id &&
            item.sub_category_id === sub_category_id
        );
      }

      if (!isEmptyOrSpaces(field_id)) {
        return userDocs.find(
          (item) =>
            item.validation_status === "approved" &&
            item.category_id === category_id &&
            item.category_subcategory_data === field_id
        );
      }
    }
    return undefined;
  };
  const tableData = (): JSX.Element[] => {
    let data: PositionRequirementTableInterface[] = props.requirements;
    if (data.length === 0) {
      return [
        <tr key={153} className="border-b border-gray-200 hover:bg-gray-100">
          <td className="p-3 text-center" colSpan={3}>
            No Data Found
          </td>
        </tr>,
      ];
    }

    return data.map((item, index) => (
      <tr
        key={index + item.field_id}
        className={`border-b border-gray-200 hover:bg-gray-100 ${
          !isDocumentExist(
            item.category_id,
            item.subcategory_id,
            item.field_id
          ) && "text-yellow-800"
        }`}
      >
        <td className="p-3 text-left whitespace-nowrap">{index + 1})</td>
        <td className="p-3 text-left">
          <span>
            {isEmptyOrSpaces(item.field_id)
              ? "Any " + item.subcategory_title
              : item.subcategory_title + " in " + item.field_title}
          </span>
        </td>
        <td className={`p-3 text-left text-lg italic`}>
          {isDocumentExist(item.category_id, item.subcategory_id, item.field_id)
            ? "Found"
            : "Not found"}
        </td>
        <td className={`p-3 text-right text-lg italic`}>
          {isDocumentExist(
            item.category_id,
            item.subcategory_id,
            item.field_id
          ) && (
            <button
              className="bg-gray-200 hover:bg-gray-300 rounded font-medium px-2 py-1 focus:outline-none"
              onClick={() => {
                setViewDoc(
                  isDocumentExist(
                    item.category_id,
                    item.subcategory_id,
                    item.field_id
                  )!
                );
                setLoadingImage(true);
              }}
            >
              View Doc
            </button>
          )}
        </td>
      </tr>
    ));
  };
  const handleFullScreen = () => {
    if (isFullscreen) {
      fullScreen("doc-img", "CLOSE");
      setSetFullscreen(false);
    } else {
      setSetFullscreen(true);
      fullScreen("doc-img", "OPEN");
    }
  };

  return (
    <div>
      <Table
        columns={[
          { columnTitle: "#", alignText: "left", padding: "p-3" },
          { columnTitle: "Qualification", alignText: "left", padding: "p-3" },
          { columnTitle: "Status", alignText: "left", padding: "p-3" },
          { columnTitle: "", alignText: "left", padding: "p-3" },
        ]}
        rows={tableData}
        pagination={false}
        search={false}
        thColor="gray"
      />

      {viewDoc && (
        <Modal
          backDrop={true}
          theme={Themes.default}
          close={() => {
            setViewDoc(null);
            setLoadingImage(false);
          }}
          backDropClose={false}
          widthSizeClass={ModalSize.extraLarge}
          displayClose={true}
          padding={{
            title: true,
            body: false,
            footer: false,
          }}
          title="Doc view"
        >
          {failedToLoadFile && (
            <NoSession
              title="Error"
              description={
                isFileImage(getFileType(viewDoc.files))
                  ? "Failed to load image"
                  : "Failed to load pdf"
              }
            />
          )}
          {loadingImage && !failedToLoadFile && <Loading />}
          {viewDoc.files && viewDoc.files.length > 0 ? (
            isFileImage(getFileType(viewDoc.files)) ? (
              <div className="relative">
                <img
                  src={API.AUTH_FILES + viewDoc.files}
                  alt=""
                  className={`w-full md:w-auto h-auto md:max-h-screen mx-auto ${
                    loadingImage && "hidden"
                  }`}
                  id="doc-img"
                  onLoad={() => setLoadingImage(false)}
                  onDoubleClick={() => handleFullScreen()}
                />
              </div>
            ) : (
              <ViewPDF
                file_url={API.AUTH_FILES + viewDoc.files}
                class_name={`w-full h-screen md:max-h-screen mx-auto ${
                  loadingImage && "hidden"
                }`}
                frame_title={"application PDF file view"}
                setLoadingFile={setLoadingImage}
                setLoadingError={setFailedToLoadFile}
              />
            )
          ) : (
            <div className="text-center text-red-400 py-8 animate__animated animate__fadeIn">
              <CgImage className="mx-auto w-40 h-40" />
              <span className="font-semibold text-4xl">No Files Provided</span>
            </div>
          )}
        </Modal>
      )}
    </div>
  );
};

export default React.memo(ViewDocRequirements);
