import React from "react";
import axios from "axios";
import { FaRegCheckCircle } from "react-icons/fa";
import { MdCancel, MdInfoOutline } from "react-icons/md";
import { RiArrowGoBackFill } from "react-icons/ri";
import { connect } from "react-redux";
import { adminDisplayClaimTitle } from ".";
import { Auth, PushPopupAlert } from "../../../actions";
import { evaluateClaim } from "../../../actions/claiming";
import { verifyDocument } from "../../../actions/document";
import { VerifyDocumentInterface } from "../../../actions/users";
import {
  CandidateClaimInterface,
  ClaimDecisionStatus,
  claimReasonInterface,
  ClaimStatus,
  ClaimTypesInterfaceAPI,
} from "../../../interfaces";
import { PopupAlertTheme } from "../../../interfaces/popupAlert";
import { StoreState } from "../../../reducers";
import { DocumentReducerInterface } from "../../../reducers/document";
import {
  capitalizeFirstLetter,
  isEmptyOrSpaces,
} from "../../../shared/dataTest";
import {
  DocValidationStatusInterface,
  UserDocumentsInterface,
} from "../../../shared/interfaces";
import { API } from "../../../utils/api";
import { errorToText } from "../../../utils/errors";
import { getFileType, isFileImage, showDate } from "../../../utils/functions";
import { isClaimEvaluated } from "../ClaimDocView";
import ViewPDF from "../../../components/ViewPDF/ViewPDF";

export type claimResponseBody = {
  sub_claim_id: string;
  evaluation_comment: string;
  evaluated_by: string;
  evaluator_name: string;
  evaluator_email: string;
  decision_taken_status: ClaimDecisionStatus;
};

type AdminClaimDocViewProps = {
  thisDoc: UserDocumentsInterface;
  documentCategories: DocumentReducerInterface;
  thisClaim: CandidateClaimInterface;
  claimReasons: claimReasonInterface[] | null;
  candidateId: string;
  claimTypes: ClaimTypesInterfaceAPI[];
  viewImage: boolean;
  setViewImage: (value: boolean) => void;
  loadingRequest: boolean;
  setLoadingRequest: (value: boolean) => void;
  actionTaken: ClaimDecisionStatus | null;
  setActionTaken: (value: ClaimDecisionStatus | null) => void;
  responseMsg: string;
  setResponseMsg: (value: string) => void;
  formError: {
    element: string;
    msg: string;
  } | null;
  setFormError: (
    value: {
      element: string;
      msg: string;
    } | null
  ) => void;

  // REDUX ==================
  auth: Auth;
  verifyDocument: Function;
  evaluateClaim: Function;
  PushPopupAlert: Function;
};

const AdminClaimDocView: React.FC<AdminClaimDocViewProps> = (props) => {
  const handleVerify = async () => {
    if (!props.actionTaken) {
      props.setFormError({
        element: "action",
        msg: "Action is required!",
      });
      return props.setLoadingRequest(false);
    }

    if (isEmptyOrSpaces(props.responseMsg)) {
      props.setFormError({
        element: "comment",
        msg: "Reason of rejection is required",
      });
      return props.setLoadingRequest(false);
    }

    let subClaim = props.thisClaim.claims.find(
      (item) => item.level === props.thisClaim.claim_level
    );

    if (subClaim) {
      props.setLoadingRequest(true);
      const api_body: claimResponseBody = {
        sub_claim_id: subClaim._id,
        evaluation_comment: props.responseMsg,
        evaluated_by: props.auth.user.user_id,
        decision_taken_status: props.actionTaken,
        evaluator_name: props.auth.user.fname + " " + props.auth.user.lname,
        evaluator_email: props.auth.user.email,
      };

      try {
        const res = await axios.put<CandidateClaimInterface>(
          `${API.CLAIMING}/user/evaluate/${props.thisClaim._id}`,
          api_body
        );
        if (res) {
          console.log({ evaluate_claim_res: res });
          props.evaluateClaim(res.data);
          // REVALIDATED DOCUMENT IF IT IS APPROVED =========================
          if (props.actionTaken === ClaimDecisionStatus.ACCEPTED) {
            await FC_DocValidation();
          }
          props.PushPopupAlert({
            title: `Claim responded successfully!`,
            theme: PopupAlertTheme.success,
          });
          // setDocumentId("null");
          props.setResponseMsg("");
          props.setActionTaken(null);
        }
      } catch (error) {
        console.error(error);
        props.PushPopupAlert({
          title: errorToText(error),
          theme: PopupAlertTheme.danger,
        });
      }
      props.setLoadingRequest(false);
    }
  };

  const FC_DocValidation = async () => {
    const api_body: VerifyDocumentInterface = {
      user_id: props.thisClaim.user.user_id,
      doc_id: props.thisClaim.claiming_on_id,
      validation_status:
        props.actionTaken === ClaimDecisionStatus.ACCEPTED
          ? DocValidationStatusInterface.APPROVED
          : DocValidationStatusInterface.REJECTED,
      validated_by: {
        user_id: props.auth.user.user_id,
        comment: props.responseMsg,
        validation_status:
          props.actionTaken === ClaimDecisionStatus.ACCEPTED
            ? DocValidationStatusInterface.APPROVED
            : DocValidationStatusInterface.REJECTED,
      },
      in_education: false,
      disability: false,
      expiration_date: "this.state.expiration_date",
    };
    const api_redux: VerifyDocumentInterface = {
      user_id: props.thisClaim.user.user_id,
      doc_id: props.thisClaim.claiming_on_id,
      validation_status:
        props.actionTaken === ClaimDecisionStatus.ACCEPTED
          ? DocValidationStatusInterface.APPROVED
          : DocValidationStatusInterface.REJECTED,
      validated_by: {
        user_id: props.auth.user.user_id,
        comment: props.responseMsg,
        validation_status:
          props.actionTaken === ClaimDecisionStatus.ACCEPTED
            ? DocValidationStatusInterface.APPROVED
            : DocValidationStatusInterface.REJECTED,
        date: "",
      },
      in_education: false,
      disability: false,
      expiration_date: "this.state.expiration_date",
    };

    try {
      const res = await axios.post<VerifyDocumentInterface>(
        `${API.USER}/docs/validateuserdoc`,
        api_body
      );
      if (res) {
        props.verifyDocument(api_redux);
        await FC_shortListing(props.thisClaim.user.user_id);
        // setDocumentId("null");
      }
    } catch (error) {
      console.error(error);
      props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
  };

  const FC_shortListing = async (candidate_id: string) => {
    try {
      const res = await axios.patch(
        `${API.APPLICATION}/apply/doccheckshortlist/${candidate_id}`
      );
      console.log({ res });
    } catch (error) {
      console.error({ error });
      props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
  };

  const getReasonById = (
    reason_id: string
  ): claimReasonInterface | undefined => {
    if (props.claimReasons) {
      return props.claimReasons.find((item) => item.id === reason_id);
    }
    return undefined;
  };

  return (
    <section className="">
      <div className="w-full bg-white px-4 py-1 divide-y-2 space-y-2">
        {/* DOCUMENT DETAILS ==================================================== */}
        <section className="flex flex-col">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-2">
              <span className={`italic text-gray-600`}>Names:</span>
              <span className="text-gray-800 font-bold text-xl">
                {props.thisClaim.user.user_info.fname +
                  " " +
                  props.thisClaim.user.user_info.lname}
              </span>
            </div>
            <button
              type="button"
              className="bg-blue-200 hover:bg-blue-300 text-blue-700 group-hover:text-blue-800 py-1 px-2 mr-1 rounded text-sm"
              onClick={() => props.setViewImage(!props.viewImage)}
            >
              {props.viewImage ? "Hide" : "View"}
            </button>
          </div>
          <div className="flex items-center gap-2">
            <span className={`italic text-gray-600`}>Title:</span>
            <span className="text-gray-800 font-bold text-2xl">
              {/* {ClaimGetDocTitle(props.documentCategories, props.thisDoc).main} */}
              {props.thisClaim.user.details.filter(
                (item) => item.validation_status !== "waiting"
              ).length > 0
                ? adminDisplayClaimTitle(
                    props.documentCategories,
                    props.thisClaim.user.details.filter(
                      (item) => item.validation_status !== "waiting"
                    ),
                    props.thisClaim
                  )
                : "-"}
            </span>
          </div>
          <div className="flex items-center gap-2 text-sm text-gray-600">
            <span className={`italic`}>Status:</span>
            <span className="font-bold text-yellow-900 rounded px-3 py-1">
              {props.thisClaim.previous
                ? capitalizeFirstLetter(props.thisClaim.previous.status)
                : capitalizeFirstLetter(props.thisDoc.validation_status)}
            </span>
          </div>
          {/* <div className="flex items-center gap-2 text-sm text-gray-600">
            <span className={`italic`}>Evaluated by:</span>
            <span className="font-semibold">
              {props.thisClaim.previous.evaluater.name}
            </span>
          </div> */}
          <div className="flex items-start gap-2 text-sm text-gray-600">
            <span className={`italic`}>Reason:</span>
            <span className="font-normal">
              {props.thisClaim.previous
                ? props.thisClaim.previous.reason
                : props.thisDoc.validated_by.commet}
            </span>
          </div>

          {/* DISPLAY IMAGE ==================================================== */}
          {props.viewImage && (
            <div className="w-full my-1 animate__animated animate__fadeIn">
              {isFileImage(getFileType(props.thisDoc.files)) ? (
                <img
                  src={API.AUTH_FILES + props.thisDoc.files}
                  alt="Applicant document"
                  className={`w-full md:w-auto h-auto md:max-h-screen mx-auto`}
                  id="doc-img"
                />
              ) : (
                <ViewPDF
                  file_url={API.AUTH_FILES + props.thisDoc.files}
                  class_name={`w-full h-screen md:max-h-screen mx-auto`}
                  frame_title={"application PDF file view"}
                />
              )}
            </div>
          )}
          {/* END DISPLAY IMAGE ==================================================== */}
        </section>
        {/* END DOCUMENT DETAILS ==================================================== */}

        {/* ################################################################################ */}

        {/* CLAIM LEVEL 1 DETAILS ==================================================== */}
        {props.thisClaim.claims.length > 0 &&
          props.thisClaim.claims.map((claim, index) => (
            <section
              className="flex flex-col px-2 pt-3"
              key={index + claim._id}
            >
              <div className="text-gray-700 italic font-bold">
                Claim Comment (level {index + 1})
              </div>
              {/* CLAIM COMMENT ==>> LEVEL 1 ==================================================== */}
              <div className="flex flex-col items-start gap-2 mt-1 border rounded px-4 py-1 bg-gray-50">
                <div className="flex items-center gap-2 bg-blue-100 rounded py-1 px-2">
                  <MdInfoOutline />
                  {getReasonById(props.thisClaim.default_claim_resaons)
                    ? getReasonById(props.thisClaim.default_claim_resaons)!
                        .reason
                    : ""}
                </div>
                <p>{claim.reason}</p>
                <span className="text-gray-500 text-xs">
                  {showDate(claim.createdAt)}
                </span>
              </div>
              {/* CLAIM COMMENT ==>> LEVEL 1 ==================================================== */}

              {isClaimEvaluated(props.thisClaim, claim.level) && (
                <section className="mt-4">
                  <span className="text-gray-700 italic font-semibold">
                    Response (level {index + 1})
                  </span>
                  <div className="border-t-2">
                    <div className="flex items-center gap-2 text-sm text-gray-600">
                      <span className={`italic`}>Status:</span>
                      <span
                        className={`font-bold ${
                          claim.decision_taken_status ===
                          ClaimDecisionStatus.REJECTED
                            ? "text-yellow-900"
                            : "text-blue-600"
                        } rounded px-3 py-1`}
                      >
                        {capitalizeFirstLetter(
                          claim.decision_taken_status.toLocaleLowerCase()
                        )}
                      </span>
                    </div>
                    {/* <div className="flex items-center gap-2 text-sm text-gray-600">
                      <span className={`italic`}>Evaluated by:</span>
                      <span className="font-semibold">
                        {claim.evaluater.name}
                      </span>
                    </div> */}
                    <div className="flex items-start gap-2 text-sm text-gray-600">
                      <span className={`italic`}>Reason:</span>
                      <span className="font-normal">
                        {claim.evaluation_comment}
                      </span>
                    </div>
                  </div>
                </section>
              )}
            </section>
          ))}
        {/* END CLAIM LEVEL 1 DETAILS ==================================================== */}
      </div>

      {/* FOOTER INITIAL ========================================== */}
      {!props.actionTaken && (
        <section className="flex items-center justify-between bg-gray-50 rounded mt-3">
          {/* SHOWING STATUS ===================================== */}
          <div className="mx-4">
            <span
              className={`${
                props.thisClaim.last_claim_status === ClaimStatus.PENDING
                  ? "text-yellow-700"
                  : props.thisClaim.last_claim_status === ClaimStatus.DELETED
                  ? "text-red-700"
                  : "text-green-700"
              } italic font-semibold`}
            >
              {capitalizeFirstLetter(
                props.thisClaim.last_claim_status.toLocaleLowerCase()
              )}
            </span>
          </div>

          {props.thisClaim.last_claim_status === ClaimStatus.PENDING && (
            <div className="flex items-center gap-2 px-2">
              <button
                className={`px-4 py-2 font-semibold text-red-50 rounded inline-flex items-center space-x-2 focus:outline-none bg-red-700 hover:bg-red-800`}
                onClick={() => {
                  props.setActionTaken(ClaimDecisionStatus.REJECTED);
                }}
              >
                <MdCancel className="w-5 h-5" />
                <span>Reject</span>
              </button>
              <button
                className={`px-4 py-2 font-semibold text-blue-50 rounded inline-flex items-center space-x-2 focus:outline-none bg-blue-700 hover:bg-blue-800`}
                onClick={() => {
                  props.setActionTaken(ClaimDecisionStatus.ACCEPTED);
                }}
              >
                <FaRegCheckCircle className="w-5 h-5" />
                <span>Accept</span>
              </button>
            </div>
          )}
        </section>
      )}
      {/* END FOOTER INITIAL ========================================== */}

      {/* ======================================================== */}
      {/* FOOTER RESPONSE ========================================== */}
      {props.actionTaken && (
        <section className="my-5 overflow-x-auto">
          <div className="italic text-gray-700 text-base my-1 mx-2">
            Selected decision:{" "}
            <span
              className={`font-semibold text-lg ${
                props.actionTaken === ClaimDecisionStatus.ACCEPTED
                  ? "text-blue-700"
                  : "text-yellow-800"
              }`}
            >
              {props.actionTaken === ClaimDecisionStatus.ACCEPTED
                ? "Approved"
                : "Rejected"}
            </span>
          </div>
          {/* right part */}
          <div className="flex flex-col md:flex-row items-end md:items-start gap-1 w-full">
            <div className="w-full px-2">
              <textarea
                placeholder={`Reason of your decision...`}
                cols={50}
                rows={3}
                className={`border border-${
                  props.formError && props.formError.element === "comment"
                    ? "red"
                    : "gray"
                }-400 rounded px-3 py-1 w-full focus:outline-none`}
                style={{ resize: "none" }}
                value={props.responseMsg}
                onChange={(e) => {
                  props.setResponseMsg(e.target.value);
                  props.setFormError(null);
                }}
                disabled={props.loadingRequest}
              ></textarea>
              {props.formError && props.formError.element === "comment" && (
                <p className="text-red-800">{props.formError.msg}</p>
              )}
            </div>
            <div className="flex flex-row md:flex-col gap-1 max-w-full px-2">
              <button
                className="bg-gray-700 hover:bg-gray-800 px-4 py-2 font-semibold rounded text-white inline-flex items-center space-x-2 focus:outline-none"
                onClick={() => {
                  props.setResponseMsg("");
                  props.setActionTaken(null);
                  props.setFormError(null);
                }}
                disabled={props.loadingRequest}
              >
                <RiArrowGoBackFill className="w-5 h-5" />
                <span>Back</span>
              </button>
              <button
                className="bg-blue-700 hover:bg-blue-800 px-4 py-2 font-semibold rounded text-white inline-flex items-center space-x-2 focus:outline-none"
                disabled={props.loadingRequest}
                onClick={() =>
                  window.confirm(
                    `Are you sure you want to ${
                      props.actionTaken === ClaimDecisionStatus.ACCEPTED
                        ? "approve"
                        : "rejected"
                    } this document?`
                  ) && handleVerify()
                }
              >
                {props.loadingRequest ? (
                  <div className="">Loading...</div>
                ) : (
                  <div className="inline-flex items-center space-x-2 gap-1">
                    <FaRegCheckCircle className="w-5 h-5" />
                    <span>Confirm</span>
                  </div>
                )}
              </button>
            </div>
          </div>
          {/* end right part */}
        </section>
      )}
      {/* END FOOTER RESPONSE ========================================== */}
    </section>
  );
};

const mapStateToProps = ({
  auth,
}: StoreState): {
  auth: Auth;
} => {
  return {
    auth: auth,
  };
};

export default React.memo(
  connect(mapStateToProps, {
    evaluateClaim,
    verifyDocument,
    PushPopupAlert,
  })(AdminClaimDocView)
);
