import axios from "axios";
import React, { useEffect, useState } from "react";
import { FaRegCheckCircle } from "react-icons/fa";
import { MdCancel, MdInfoOutline } from "react-icons/md";
import { RiArrowGoBackFill } from "react-icons/ri";
import { connect } from "react-redux";
import {
  Auth,
  loadQuestionTypes,
  PushPopupAlert,
  questionTypesStoreInterface,
} from "../../../actions";
import { evaluateClaim } from "../../../actions/claiming";
import { AnswersDetails } from "../../../components/AnswersDetails/AnswersDetails";
import Loading from "../../../components/Loading/Loading";
import {
  CandidateClaimInterface,
  ClaimDecisionStatus,
  ClaimExamResultsDetails,
  claimReasonInterface,
  ClaimStatus,
  ClaimTypesInterfaceAPI,
  course,
} from "../../../interfaces";
import { ExamAnswerSheetDone } from "../../../interfaces/exam";
import { PopupAlertTheme } from "../../../interfaces/popupAlert";
import { StoreState } from "../../../reducers";
import {
  capitalizeFirstLetter,
  isEmptyOrSpaces,
} from "../../../shared/dataTest";
import { API } from "../../../utils/api";
import { errorToText } from "../../../utils/errors";
import { showDate } from "../../../utils/functions";
import { claimShowAnswer } from "../Candidate/ExaminationFormView";
import { isClaimEvaluated } from "../ClaimDocView";
import { claimResponseBody } from "./AdminClaimDocView";

enum viewTabs {
  profile = "Applicant Profile",
  exam = "Exam Details",
}

type AdminClaimExamViewProps = {
  thisExamSheet: ClaimExamResultsDetails;
  thisExam: course;
  thisClaim: CandidateClaimInterface;
  claimTypes: ClaimTypesInterfaceAPI[];
  claimReasons: claimReasonInterface[] | null;

  // REDUX ==================
  auth: Auth;
  questionTypes: questionTypesStoreInterface;
  evaluateClaim: Function;
  loadQuestionTypes: (callback: (loading: boolean) => void) => void;
  PushPopupAlert: Function;
};

const AdminClaimExamView: React.FC<AdminClaimExamViewProps> = (props) => {
  const [loadingRequest, setLoadingRequest] = useState<boolean>(false);
  const [loading, setLoading] = useState(true);
  const [actionTaken, setActionTaken] = useState<ClaimDecisionStatus | null>(
    null
  );
  const [formError, setFormError] = useState<{
    element: string;
    msg: string;
  } | null>(null);
  const [responseMsg, setResponseMsg] = useState<string>("");
  const [marks, setMarks] = useState<number>(0);
  const [selectedTab, setSelectedTab] = useState<viewTabs>(viewTabs.profile);

  useEffect(() => {
    if (
      props.questionTypes.questionTypes === null ||
      (props.questionTypes.questionTypes.length === 0 && loading)
    ) {
      props.loadQuestionTypes(setLoading);
    } else if (loading) {
      setLoading(false);
    }
  }, [loading, props]);

  if (loading || props.questionTypes.questionTypes === null) {
    return <Loading />;
  }

  const handleVerify = async () => {
    if (!actionTaken) {
      setFormError({
        element: "action",
        msg: "Action is required!",
      });
      return setLoadingRequest(false);
    }

    if (
      actionTaken === ClaimDecisionStatus.ACCEPTED &&
      (marks <= 0 || marks > 100)
    ) {
      setFormError({
        element: "marks",
        msg: "Invalid marks, marks must be between 1 and 10",
      });

      return setLoadingRequest(false);
    }

    if (isEmptyOrSpaces(responseMsg)) {
      setFormError({
        element: "comment",
        msg: "Reason of rejection is required",
      });
      return setLoadingRequest(false);
    }

    let subClaim = props.thisClaim.claims.find(
      (item) => item.level === props.thisClaim.claim_level
    );

    if (subClaim) {
      setLoadingRequest(true);
      const api_body: claimResponseBody = {
        sub_claim_id: subClaim._id,
        evaluation_comment: responseMsg,
        evaluated_by: props.auth.user.user_id,
        decision_taken_status: actionTaken,
        evaluator_name: props.auth.user.fname + " " + props.auth.user.lname,
        evaluator_email: props.auth.user.email,
      };

      try {
        const res = await axios.put<CandidateClaimInterface>(
          `${API.CLAIMING}/user/evaluate/${props.thisClaim._id}`,
          api_body
        );
        if (res) {
          console.log({ evaluate_claim_res: res });
          props.evaluateClaim(res.data);
          // REVALIDATED DOCUMENT IF IT IS APPROVED =========================
          if (actionTaken === ClaimDecisionStatus.ACCEPTED) {
            await handleMarksAddition();
          }
          props.PushPopupAlert({
            title: `Claim responded successfully!`,
            theme: PopupAlertTheme.success,
          });
          // setDocumentId("null");
          setResponseMsg("");
          setActionTaken(null);
        }
      } catch (error) {
        console.error(error);
        props.PushPopupAlert({
          title: errorToText(error),
          theme: PopupAlertTheme.danger,
        });
      }
      setLoadingRequest(false);
    }
  };

  const handleMarksAddition = async () => {
    try {
      await axios.put(`${API.DO_EXAM}/answersheet/user/marks/update`, {
        answer_sheet_id: props.thisClaim.claiming_on_id,
        marks: marks,
      });
    } catch (error) {
      console.error(error);
      props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
  };

  const getReasonById = (
    reason_id: string
  ): claimReasonInterface | undefined => {
    if (props.claimReasons) {
      return props.claimReasons.find((item) => item.id === reason_id);
    }
    return undefined;
  };

  // const getAnswersByQuestion = (question_id: string): ClaimGetUserAnswers[] => {
  //   return props.thisExamSheet.user_answers.filter(
  //     (item) => item.question === question_id
  //   );
  // };

  const claimCandidateAnswers = (): ExamAnswerSheetDone[] => {
    let result: ExamAnswerSheetDone[] = [
      {
        exam_location: props.thisExamSheet.exam_location,
        questions: props.thisExamSheet.questions,
        user_answers: props.thisExamSheet.user_answers,
        user: props.thisExamSheet.user,
        period: props.thisExamSheet.period,
        course: props.thisExamSheet.course,
        exam_paper: props.thisExamSheet.exam_paper,
        user_status: props.thisExamSheet.user_status,
        used_duration: props.thisExamSheet.used_duration,
        total_marks: props.thisExamSheet.total_marks,
        number_of_question_answered:
          props.thisExamSheet.number_of_question_answered,
        // start_time: string,
        // end_time: string,
        exam_total_marks: props.thisExamSheet.exam_total_marks,
        exam_start_time: props.thisExamSheet.exam_start_time,
        exam_end_time: props.thisExamSheet.exam_end_time,
        exam_date: props.thisExamSheet.exam_date,
        status: props.thisExamSheet.status,
        createdAt: props.thisExamSheet.createdAt,
        updatedAt: props.thisExamSheet.updatedAt,
        disability: false,
        gender: "",
        in_education: false,
        position: "",
        answer_sheet_id: props.thisExamSheet.answer_sheet_id,
      },
    ];
    // props.thisExamSheet

    return result;
  };

  return (
    <section className="">
      {/* #################### VIEW TABS ################################## */}
      <div className="flex flex-row items-center mx-4">
        {Object.values(viewTabs).map((item, i) => (
          <div
            key={i}
            onClick={() => setSelectedTab(item)}
            className={`animate__animated ${
              selectedTab === item
                ? "bg-blue-100 border-blue-600 text-blue-600 animate__zoomIn"
                : "bg-white border-white text-gray-600"
            } hover:bg-blue-100 px-4 py-2 cursor-pointer font-bold border-b-4 hover:border-blue-600 hover:text-blue-600 rounded-t mt-1 mr-1`}
          >
            {item}
          </div>
        ))}
      </div>
      {/* #################### END VIEW TABS ################################## */}
      <div className="w-full bg-white px-4 py-1 divide-y-2 space-y-2">
        {/* DOCUMENT DETAILS ==================================================== */}
        <section className="flex flex-col">
          {/* DISPLAY POSITION ==================================================== */}
          {selectedTab === viewTabs.exam && (
            <section className="w-full my-1 animate__animated animate__fadeIn rounded">
              <div className="bg-white w-full rounded p-2 mb-2">
                <AnswersDetails
                  showAnswers={true}
                  exam_id={claimShowAnswer(
                    props.thisExamSheet,
                    props.thisExam.course_name
                  )}
                  allAnswers={claimCandidateAnswers()}
                  user_id={props.thisExamSheet.user}
                />
              </div>
            </section>
          )}
          {/* END DISPLAY POSITION ==================================================== */}
          {/* -------------------------------------------------------------------------------- */}
          {/* DISPLAY PROFILE ==================================================== */}
          {selectedTab === viewTabs.profile && (
            <section className="w-full my-2 animate__animated animate__fadeIn rounded">
              <div className="flex items-center justify-between">
                <div className="flex items-center gap-2">
                  <span className={`italic text-gray-600`}>Names:</span>
                  <span className="text-gray-800 font-bold text-xl">
                    {props.thisClaim.user.user_info.fname +
                      " " +
                      props.thisClaim.user.user_info.lname}
                  </span>
                </div>
              </div>
            </section>
          )}
          {/* END DISPLAY PROFILE ==================================================== */}
          {selectedTab === viewTabs.profile && (
            <div className="">
              <div className="flex items-center gap-2">
                <span className={`italic text-gray-600`}>Email:</span>
                <span className="text-gray-800 font-bold text-lg">
                  {props.thisClaim.user.email}
                </span>
              </div>
              <div className="flex items-center gap-2">
                <span className={`italic text-gray-600`}>Phone:</span>
                <span className="text-gray-800 font-bold text-lg">
                  {props.thisClaim.user.phone_number}
                </span>
              </div>
              <div className="flex items-center gap-2">
                <span className={`italic text-gray-600`}>Gender:</span>
                <span className="text-gray-800 font-bold text-lg">
                  {props.thisClaim.user.user_info.sex === "M"
                    ? "Male"
                    : "Female"}
                </span>
              </div>
            </div>
          )}
        </section>
        {/* END DOCUMENT DETAILS ==================================================== */}

        {/* ################################################################################ */}

        {/* CLAIM LEVEL 1 DETAILS ==================================================== */}
        {props.thisClaim.claims.length > 0 &&
          props.thisClaim.claims.map((claim, index) => (
            <section
              className="flex flex-col px-2 pt-3"
              key={index + claim._id}
            >
              <div className="text-gray-700 italic font-bold">
                Claim Comment (level {index + 1})
              </div>
              {/* CLAIM COMMENT ==>> LEVEL 1 ==================================================== */}
              <div className="flex flex-col items-start gap-2 mt-1 border rounded px-2 py-1 bg-gray-50">
                <div className="flex items-center gap-2 bg-blue-100 rounded py-1 px-2">
                  <MdInfoOutline />
                  {getReasonById(props.thisClaim.default_claim_resaons)
                    ? getReasonById(props.thisClaim.default_claim_resaons)!
                        .reason
                    : ""}
                </div>
                <p>{claim.reason}</p>
                <span className="text-gray-600 text-xs">
                  {showDate(claim.createdAt)}
                  {/* {showDate(claim.createdAt) +
                    " at " +
                    moment.unix(claim.date_created).format("MM/DD/YYYY")} */}
                </span>
              </div>
              {/* CLAIM COMMENT ==>> LEVEL 1 ==================================================== */}

              {isClaimEvaluated(props.thisClaim, claim.level) && (
                <section className="mt-4">
                  <span className="text-gray-700 italic font-semibold">
                    Response (level {index + 1})
                  </span>
                  <div className="border-t-2">
                    <div className="flex items-center gap-2 text-sm text-gray-600">
                      <span className={`italic`}>Status:</span>
                      <span
                        className={`font-bold ${
                          claim.decision_taken_status ===
                          ClaimDecisionStatus.REJECTED
                            ? "text-yellow-900"
                            : "text-blue-600"
                        } rounded px-3 py-1`}
                      >
                        {capitalizeFirstLetter(
                          claim.decision_taken_status.toLocaleLowerCase()
                        )}
                      </span>
                    </div>
                    {/* <div className="flex items-center gap-2 text-sm text-gray-600">
                      <span className={`italic`}>Evaluated by:</span>
                      <span className="font-semibold">
                        {claim.evaluater.name}
                      </span>
                    </div> */}
                    <div className="flex items-start gap-2 text-sm text-gray-600">
                      <span className={`italic`}>Reason:</span>
                      <span className="font-normal">
                        {claim.evaluation_comment}
                      </span>
                    </div>
                  </div>
                </section>
              )}
            </section>
          ))}
        {/* END CLAIM LEVEL 1 DETAILS ==================================================== */}
      </div>

      {/* FOOTER INITIAL ========================================== */}
      {!actionTaken && (
        <section className="flex items-center justify-between bg-gray-50 rounded mt-3">
          {/* SHOWING STATUS ===================================== */}
          <div className="mx-4 my-4">
            <span
              className={`${
                props.thisClaim.last_claim_status === ClaimStatus.PENDING
                  ? "text-yellow-700"
                  : props.thisClaim.last_claim_status === ClaimStatus.DELETED
                  ? "text-red-700"
                  : "text-green-700"
              } italic font-semibold`}
            >
              {capitalizeFirstLetter(
                props.thisClaim.last_claim_status.toLocaleLowerCase()
              )}
            </span>
          </div>

          {props.thisClaim.last_claim_status === ClaimStatus.PENDING && (
            <div className="flex items-center gap-2 px-2">
              <button
                className={`px-4 py-2 font-semibold text-red-50 rounded inline-flex items-center space-x-2 focus:outline-none bg-red-700 hover:bg-red-800`}
                onClick={() => {
                  setActionTaken(ClaimDecisionStatus.REJECTED);
                }}
              >
                <MdCancel className="w-5 h-5" />
                <span>Reject</span>
              </button>
              <button
                className={`px-4 py-2 font-semibold text-blue-50 rounded inline-flex items-center space-x-2 focus:outline-none bg-blue-700 hover:bg-blue-800`}
                onClick={() => {
                  setActionTaken(ClaimDecisionStatus.ACCEPTED);
                }}
              >
                <FaRegCheckCircle className="w-5 h-5" />
                <span>Accept</span>
              </button>
            </div>
          )}
        </section>
      )}
      {/* END FOOTER INITIAL ========================================== */}

      {/* ======================================================== */}
      {/* FOOTER RESPONSE ========================================== */}
      {actionTaken && (
        <section className="my-5 overflow-x-auto">
          <div className="italic text-gray-700 text-base my-1 mx-2">
            Selected decision:{" "}
            <span
              className={`font-semibold text-lg ${
                actionTaken === ClaimDecisionStatus.ACCEPTED
                  ? "text-blue-700"
                  : "text-yellow-800"
              }`}
            >
              {actionTaken === ClaimDecisionStatus.ACCEPTED
                ? "Approved"
                : "Rejected"}
            </span>
          </div>
          {/* right part */}
          <div className="flex flex-col md:flex-row items-end md:items-start gap-1 w-full">
            <div className="w-full space-y-1">
              {actionTaken === ClaimDecisionStatus.ACCEPTED && (
                <div className="flex items-center justify-end w-full gap-2">
                  <span className="font-semibold">Addition marks</span>
                  <div className="px-2">
                    <input
                      placeholder={`Addition marks`}
                      className={`border border-${
                        formError && formError.element === "marks"
                          ? "red"
                          : "gray"
                      }-400 rounded px-3 py-1 w-auto focus:outline-none`}
                      value={marks}
                      onChange={(e) => {
                        setMarks(Number(e.target.value));
                        setFormError(null);
                      }}
                      disabled={loadingRequest}
                    />
                    {formError && formError.element === "marks" && (
                      <p className="text-red-800">{formError.msg}</p>
                    )}
                  </div>
                </div>
              )}
              <div className="w-full px-2">
                <textarea
                  placeholder={`Reason of your decision...`}
                  cols={50}
                  rows={actionTaken === ClaimDecisionStatus.ACCEPTED ? 2 : 3}
                  className={`border border-${
                    formError && formError.element === "comment"
                      ? "red"
                      : "gray"
                  }-400 rounded px-3 py-1 w-full focus:outline-none`}
                  style={{ resize: "none" }}
                  value={responseMsg}
                  onChange={(e) => {
                    setResponseMsg(e.target.value);
                    setFormError(null);
                  }}
                  disabled={loadingRequest}
                ></textarea>
                {formError && formError.element === "comment" && (
                  <p className="text-red-800">{formError.msg}</p>
                )}
              </div>
            </div>
            <div className="flex flex-row md:flex-col gap-1 max-w-full px-2">
              <button
                className="bg-gray-700 hover:bg-gray-800 px-4 py-2 font-semibold rounded text-white inline-flex items-center space-x-2 focus:outline-none"
                onClick={() => {
                  setResponseMsg("");
                  setActionTaken(null);
                  setFormError(null);
                }}
                disabled={loadingRequest}
              >
                <RiArrowGoBackFill className="w-5 h-5" />
                <span>Back</span>
              </button>
              <button
                className="bg-blue-700 hover:bg-blue-800 px-4 py-2 font-semibold rounded text-white inline-flex items-center space-x-2 focus:outline-none"
                disabled={loadingRequest}
                onClick={() =>
                  window.confirm(
                    `Are you sure you want to ${
                      actionTaken === ClaimDecisionStatus.ACCEPTED
                        ? "approve"
                        : "rejected"
                    } this document?`
                  ) && handleVerify()
                }
              >
                {loadingRequest ? (
                  <div className="">Loading...</div>
                ) : (
                  <div className="inline-flex items-center space-x-2 gap-1">
                    <FaRegCheckCircle className="w-5 h-5" />
                    <span>Confirm</span>
                  </div>
                )}
              </button>
            </div>
          </div>
          {/* end right part */}
        </section>
      )}
      {/* END FOOTER RESPONSE ========================================== */}
    </section>
  );
};

const mapStateToProps = ({
  auth,
  questionTypes,
}: StoreState): {
  auth: Auth;
  questionTypes: questionTypesStoreInterface;
} => {
  return {
    auth,
    questionTypes,
  };
};

export default React.memo(
  connect(mapStateToProps, {
    loadQuestionTypes,
    evaluateClaim,
    PushPopupAlert,
  })(AdminClaimExamView)
);
